.product-card {
    border: 1px solid #ddd;
    border-radius: 8px;
    padding: 10px;
    text-align: center;
  }
  
  .product-image {
    width: 100%;
    height: 200px;
    object-fit: cover;
    margin-bottom: 10px;
  }
  
  .product-card h4 {
    font-size: 16px;
    margin-bottom: 5px;
  }
  
  .product-card p {
    font-size: 18px;
    color: #b12704;
  }
  