.navbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 20px;
  background-color: #232f3e;
  color: white;
}

.navbar-logo a {
  font-size: 1.5rem;
  font-weight: bold;
  color: white;
  text-decoration: none;
}

.navbar-search {
  display: flex;
  align-items: center;
  flex-grow: 1;
  margin: 0 20px;
}

.navbar-search input {
  width: 100%;
  padding: 8px;
  border: none;
  border-radius: 4px 0 0 4px;
}

.search-btn {
  padding: 8px 15px;
  background-color: #febd69;
  border: none;
  border-radius: 0 4px 4px 0;
  color: black;
  cursor: pointer;
}

.search-btn:hover {
  background-color: #f90;
}

.navbar-user,
.navbar-cart {
  margin-left: 20px;
}

.navbar-user span {
  margin-right: 10px;
}

.navbar-user a,
.navbar-user button,
.navbar-cart a {
  color: white;
  text-decoration: none;
  font-size: 1rem;
  border: none;
  background: none;
  cursor: pointer;
}

.navbar-user button:hover,
.navbar-cart a:hover,
.navbar-user a:hover {
  text-decoration: underline;
}
